@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', Arial, sans-serif;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
  background-color: #013D5B;
  min-height: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
}

h1 {
  margin: 0 10%;
}

.info {
  width: 100%;
  margin: auto;
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  padding-bottom: 20px;
}

form * {
  font-size: 16px;
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  border-width: thin;
  font-family: 'Montserrat', Arial, sans-serif;
}

#input {
  border-color: black;
}

#search {
  padding: 11px;
  color: white;
  background-color: green;
  border-style: none;
}

.nav {
  font-family: 'Montserrat', Arial, sans-serif;
  padding: 10px;
  margin: 10px 10px 0 10px;
}

#searchHeader {
  padding: 20px 0;
  width: 100%;
  background-color: #eee;
}

.card {
  width: 50%;
  margin: auto;
}

.card {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-style: solid;
  border-width: 1px 0 0 0;
  padding: 5% 0;
}

.card:first-child {
  border-style: none;
}

.cardImage {
  max-width: 100px;
  vertical-align: middle;
}

.image {
  -webkit-flex: 20% 1;
          flex: 20% 1;
}

.description {
  -webkit-flex: 80% 1;
          flex: 80% 1;
  margin: 0 5%;
}

.title {
  font-size: 20px;
  padding: 10px;
}

.moreInfo {
  font-family: 'Montserrat', Arial, monospace;
  padding: 10px;
}

.ReactModal__Body--open {
  overflow: hidden;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: white;
  overflow: auto;
  border-radius: 10px;
  padding: 20px;
}

.modalOverlay {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.1);
}

.modalInfo {
  text-align: center;
}

.footer {
  background-color: #013D5B;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 20px;
  color: white;
}

h4 {
  font-weight: normal;
}

a:visited {
  color: white;
}

